.App {
  background-color: #1ac2b1;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 578px;
  height: min-content;
  padding: 30px 20px;
  box-shadow: 10px 10px 42px 0px #1ac2b1;
  border-radius: 10px;
}

button {
  cursor: pointer;
  background-color: white;
  color:#1ac2b1;
  border: none;
  padding: 5px;
  border-radius: 7px;
  font-size: 18px;
  font-weight: bold;
  width: 100%;
}


@media only screen and (max-width: 500px) {
  .App {
    width: 100vw;
  }
}
